<template>
  <div>
    <div class="title-fix">
      我的
    </div>
    <van-row
      class="image"
    >
      <van-col
        span="4"
        offset="1"
      >
        <van-image
          width="90px"
          height="90px"
          :src="info.img"
          :style="{
            'padding-bottom': '10px'
          }"
        >
        <template v-slot:loading>
          <van-loading type="spinner" size="20"></van-loading>
        </template>
        <template v-slot:error>
          <van-icon name="info-o" />
        </template>
      </van-image>
      </van-col>
      <van-col span="6" offset="4">
        <div class="wx-name">
          {{info.wechatName}}
        </div>
      </van-col>
      <van-col span="1" offset="6">
        <van-icon
          name="arrow"
          :style="{
            'line-height': '6rem',
            'color': '#FFFFFF'
          }"
        />
      </van-col>
    </van-row>
    <van-row>
      <van-col span="24">
        <van-grid
          :column-num="4"
          :border="false"
          square
          clickable
        >
          <van-grid-item
            v-for="item in navList"
            :key="item.id"
            @click="click(item.id)"
          >
            <div solt="icon">
              <van-icon :style="{
                'font-size': '2rem',
                'color': item.color ? item.color : '#000000'
              }"
              :name="item.icon"></van-icon>
            </div>
            <div solt="text">{{item.name}}</div>
          </van-grid-item>
        </van-grid>
      </van-col>
    </van-row>
    <van-overlay :show="showInput">
      <div class="passPopup">
        <div class="content">
          <div class="closse">
            <van-icon
              name="cross"
              @click="overLayClick()"
            />
          </div>
          <div class="title">请输入支付密码</div>
        </div>
        <div class="total"></div>
        <van-password-input
          v-if="showInput"
          :value="password"
          info="请输入6位密码"
          :focused="showKeyboard"
          @focus="passwordInput"
        />
      </div>
      <van-number-keyboard
        :show="numberShow"
        @input="onInput"
        @delete="onDeleted"
      />
    </van-overlay>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      info: {},
      navList: [
        {
          id: 1,
          icon: 'coupon-o',
          name: '全部订单',
          color: '#1989fa'
        },
        {
          id: 2,
          icon: 'todo-list-o',
          name: '待付款订单',
          color: '#FF6034'
        },
        {
          id: 3,
          icon: 'balance-list-o',
          name: '已完成订单',
          color: '#00D500'
        },
        // {
        //   id: 4,
        //   icon: 'like',
        //   name: '我想看的',
        //   color: '#e54847'
        // },
        {
          id: 5,
          icon: 'shopping-cart-o',
          name: '购物车',
          color: '#EE0A24'
        },
        {
          id: 6,
          icon: 'vip-card-o',
          name: 'VIP',
          color: '#FAAF00'
        },
        // {
        //   id: 7,
        //   icon: 'chat-o',
        //   name: '评论',
        //   color: ''
        // },
        // {
        //   id: 8,
        //   icon: 'location-o',
        //   name: '我去过的',
        //   color: 'blue'
        // },
        {
          id: 9,
          icon: 'setting-o',
          name: '设置',
          color: ''
        }
      ],
      name: '',
      size: 0,
      numberShow: false,
      showInput: false,
      showKeyboard: false,
      password: '',
      isShow: true,
      showPopup: false,
    }
  },
  methods: {
    onClickInfo () {
      this.$router.push('/user/info/' + 2)
    },
    loadData () {
      this.$Get(this.$API.CUSTOMER.CustmerInfo, {}).then(res => {
        this.info = res.data
      })
    },
    overLayClick () {
      this.showInput = false
      this.numberShow = false
    },
    onInput (key) {
      this.password = (this.password + key).slice(0, 6)
      if (this.password.length >= 6) {
        this.numberShow = false
        this.$Post(this.$API.CUSTOMER.CustomerVIPOpen, {
          password: this.password
        }).then(res => {
          this.$toast.success('支付成功')
          this.numberShow = false
          this.showInput = false
        })
        this.password = ''
      }
    },
    passwordInput () {
      this.numberShow = true
      this.showKeyboard = true
    },
    onDeleted () {
      this.password = this.password.slice(0, this.password.length - 1)
    },
    click (id) {
      if (id === 7 || id === 5 || id === 8) {
        this.$toast.success('该功能暂未开放')
      } else if (id == 1) {
        this.$router.push({
          name: 'order',
          params: {
            status: ''
          }
        })
        this.$emit('change', ['order'])
      } else if (id == 2) {
        this.$router.push({
          name: 'order',
          params: {
            status: 0
          }
        })
        this.$emit('change', ['order'])
      } else if (id == 3) {
        this.$router.push({
          name: 'order',
          params: {
            status: 4
          }
        })
        this.$emit('change', ['order'])
      } else if(id == 6){
        this.numberShow = true
        this.showInput = true
      }
    }
  },
  mounted () {
    this.loadData()
  }
}
</script>

<style lang="stylus" scoped>
.title-fix
  padding 0.625rem
  height 2.5rem
  width 100%
  color #FFFFFF
  position fixed
  top 0rem
  background-color #e54847
  z-index 2
.image
  background-color #e54847
  padding-top 0.9375rem
.body
  margin 0rem
.wx-name
  color #FFFFFF
  margin-top 0.625rem
  font-size 1.25rem
.passPopup
    background-color #FFFFFF
    position relative
    top 3.125rem
    width 90%
    margin 0 auto
    font-size 0.875rem
    color #000000
    .content
      position relative
      text-align center
      .closse
        text-align left
        float left
        color #EEEEEE
      .title
        display inline-block
        text-align center
        margin 0 auto
        font-family "arial black"
        font-size 1rem
    .total
      margin 0 auto
      text-align center
      font-family "arial black"
      font-weight bold
      font-size 1.375rem
    .tip
      margin 0 auto
      text-align center
      font-family "arial black"
      font-size 1rem
      color #333333
>>>.van-grid-item__content--center
  border-bottom 0.0625rem solid #EEEEEE
  border-left 0.0625rem solid #EEEEEE
>>> .van-card
  background-color #AAAAAA
  div
    color #FFFFFF
>>> .van-password-input
    background-color #FFFFFF
    padding 0.625rem
    top 25%
</style>



